
.radio_title {
  font-weight: bold;
  line-height: 40px;
  background: #0d163f;
  text-align: left;
  color: #f7f1db;
  padding-left: 20px;
  font-size: 16px;
}
.radio_items {
  background: #16235b;
  display: flex;
  justify-content: space-between;
  flex-flow: wrap;
}
.radio_item {
  width: 43.7%;
  padding: 10px;
  max-width: 400px;
  min-width: 140px;
  min-height: 167px;
}
.radio_item img {
  width: 100%;
}
.radio_item div {
  padding: 4px;
  color: #f7f1db;
}
.song_title {
  font-weight: bold;
  line-height: 40px;
  background: #0d163f;
  text-align: left;
  color: #f7f1db;
  padding-left: 32px;
}
.song_item {
  max-height: 200px;
  min-height: 133px;
  background: #16235b;
  display: flex;
  justify-content: space-between;
  border-bottom: 3px solid #0d163f;
}
.song_info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 13px 13px 13px 0;
  color: #f7f1db;
  width: 70%;
}
.song_desc {
  height: 80%;
}
.song_auth {
  display: flex;
  align-items: center;
  height: 20%;
  font-size: 12px;
  color: #f7f1db;
}
.user_img {
  width: 19px;
}
.user_name {
  margin-top: 4px;
  margin-left: 3px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
.song_img {
  padding: 13px 13px 0 13px;
  width: 160px;
}
.song_img img {
  width: 100%;
}
.song_headset {
  display: flex;
  align-items: center;
  position: absolute;
  margin: -20px 0 0 5px;
  color: #fff;
  font-size: 12px;
}
.song_headset img {
  width: 16px;
  margin-right: 4px;
}
